import React from 'react';
import MusicScore from "../music/MusicScore";
import Staff from "../components/MusicStaff";
import {lineh} from '../config/MusicConfig';
import Toolbar, {iconfont, color} from "../components/Toolbar";
import Questions, {randomInterval, randomChord} from "../questions/Questions";
import ListView from "../util/ListView";

//showListView
const [ListNone, ListLevels] = [0,1]

class MusicExam extends React.Component {
	constructor(props) {
		super(props);
		let types = this.props.match.params && this.props.match.params.type ? [this.props.match.params.type] : null
		this.state = {
			addnotes: [],
			addnotesIndex: 0,

			questionTypes: types,
			questions: Questions.genQuestions(1, types),
			questionIndex: 0, //-1 没有开始，0...n 做题中， 
			answer: -1,

			level: window.level,
			showListView: ListNone,
		};
	}
	showQuestions(offset) {
		var questionIndex = this.state.questionIndex
		var questions = this.state.questions
		if (offset && questions.length>0) {
			var index = questionIndex+offset
			if (index>=0 && index<questions.length) {
				questionIndex = index
			}else{
				return
			}
		}else {
			// console.log(this.props.match.params)
			let types = this.state.questionTypes
	        questions = Questions.genQuestions(this.state.level, types)
	        questionIndex = 0
	        if (!questions || questions.length===0) {
	        	console.log("error: genQuestions empty for type:"+types)
	        }
		}

	    return {
	    	addnotes: [],
	    	addnotesIndex: 0,

	    	questions: questions,
	    	questionIndex: questionIndex,
	    	answer: -1,
	    }
	}
	timeout(ms) {
		return new Promise((resolve, reject) => {
			setTimeout(resolve, ms, 'done');
		});
	}
	checkAnswer() {
		var question = this.state.questions[this.state.questionIndex]
		if (question && question.checkAnswer(this.state.answer, this.state.addnotes)) {
			this.timeout(1000).then((value) => {
				console.log(value);
				let newState = this.showQuestions(1)
				this.setState(prevState => (newState))
			});
			question.correct = true
			return {questionIndex: this.state.questionIndex}
			//return this.showQuestions(1)
		}
		question.correct = false
		
		return {questionIndex: this.state.questionIndex}
	}
	messageOfAnswer(question) {
		if (question.correct === undefined) {
			return
		}
		if (question.correct) {
			return "回答正确"
		}else{
			let msg = "回答错误"
			if (!question.answer.notes) {
				let answer = "ABCD".charAt(question.answer)
				msg += ` 正确答案是：${answer}`
			}
			return msg
		}
	}
	accidentalOfAlter(alter) {
		if (alter === 1) {
			return "sharp"
		}else if (alter===2) {
			return "sharp-sharp"
		}else if (alter===-1) {
			return "flat"
		}else if (alter===-2) {
			return "flat-flat"
		}
	}
	showQuestion() {
		var question = this.state.questions[this.state.questionIndex]
		if (!question) {
			console.log("error: questions is empty")
			console.log(this.state.questions)
			return
		}
		var items = []
		var title = (this.state.questionIndex+1)+". "+question.desc
	    items.push(<div key={0}>{title}</div>)

        if (question.score && question.score.notes) {
			// var elems = []
			let notes = question.score.notes.concat(this.state.addnotes)
			let elems = notes.map(function(note) {
				return {line: note.line, accidental: this.accidentalOfAlter(note.alter)}
			}, this)
	        var score = MusicScore.createScore(elems)

	        //显示参考答案
	        if (question.correct === false && question.answer.notes) {
	        	let notes = question.score.notes.concat(question.answer.notes)
	        	let correctElems = []
	        	for (let note of notes) {
	        		correctElems.push({line: note.line, accidental: this.accidentalOfAlter(note.alter)})
	        	}	
	        	let correctNote = {note: 8, elems:correctElems}
	        	score.parts[0].measures[0].staves[0].push(correctNote)
	        }

	        var cursor
	        if (this.state.addnotes.length>0) {
	        	cursor = {partIndex: 0, measureIndex: 0, staffIndex:0, noteIndex: 0, elemIndex: this.state.addnotesIndex+1}
	        }
	        items.push(<Staff key={items.length} score={score} cursor={cursor} top={60} height={160}/>)
        }
        // const ch=["A","B","C","D"]
        if (question.choices) {
        	for (let k in question.choices) {
        	// for (var k = 0; k < question.choices.length; k++) {
        		var s = question.choices[k]
        		var subtitle = "ABCD".charAt(k) + ". " + s + "  "
			    items.push(<div key={items.length}>{subtitle}</div>)
        	}
        }
    	if (question && question.correct !== undefined) {
    		// var question = this.state.questions[this.state.questionIndex]
    		let msg = this.messageOfAnswer(question)
    		items.push(<div key={items.length} style={{color: question.correct ? "blue" : "red"}}>{msg}</div>)
    	}
        return (<div>{items}</div>)
	}

	takeAction(action) {
		let newState
		switch (action.type) {
		case 'showlevel':
			newState = {showListView: ListLevels}
			break
		case 'selectLevel':
			this.state.level = parseInt(action.value)
			newState = this.showQuestions(0)
			newState.showListView = ListNone
			break
		case 'questions':
			newState = this.showQuestions(action.offset)
			break
		case 'answer':
			newState = {answer: action.value}
			break
		case 'checkAnswer':
			newState = this.checkAnswer()
			break
		case 'addNote':
			this.state.addnotes.push({line: 0})
			newState = {addnotes: this.state.addnotes, addnotesIndex: this.state.addnotes.length-1}
			break
		case 'delNote':
			this.state.addnotes.splice(this.state.addnotesIndex, 1)
			newState = {addnotes: this.state.addnotes, addnotesIndex: this.state.addnotes.length-1}
			break
		case 'moveChordLevel':
			var chord = this.state.addnotes[this.state.addnotesIndex]
			var line = chord.line+action.offset
			chord.line = line
			newState = {addnotes: this.state.addnotes}
			break
		case 'selectChord':
			var index = (this.state.addnotesIndex+1)%this.state.addnotes.length
			newState = {addnotesIndex: index}
			break
		case 'setNoteAlter':
			var note = this.state.addnotes[this.state.addnotesIndex]
			if (action.offset === 1) {
				if (note.alter === 1) {
					note.alter = 2
				}else if (note.alter === 2) {
					note.alter = 0
				}else {
					note.alter = 1
				}
			}else{
				if (note.alter === -1) {
					note.alter = -2
				}else if (note.alter === -2) {
					note.alter = 0
				}else {
					note.alter = -1
				}
			}
			newState = {addnotes: this.state.addnotes}
			break
		default:
		}
		this.setState(prevState => (newState))
	}
	get scoreToolbar() {
	    var buttons = []
		// const btnKeys = ["C", "D", "E", "F", "G", "A", "B", "C", "D", "E", "F", "G", "A", "B", "C", "D", "E", "F", "G", "A", "B", "C"]
		// for (var i = btnKeys.length - 1; i >= 0; i--) {
		// 	const key = 48+this.keyOfStep(i)
		// 	buttons.push({title:btnKeys[i], key: key, background:"rgba(0,0,0,0)", color:"#000", onClick:(e) => this.inputNoteKeys([key]) })
		// }

		buttons.push(
		// {title:"<", key: 50, action: {type: "questions", offset: -1}, background: color.pink},
		{title:"试题", key: 51, action: {type: "showlevel"}, background: color.pink},
		// {title:">", key: 52, action: {type: "questions", offset: 1}, background: color.pink},

		// {title:"音程", key: 73, action: {type: "interval"}, background: color.pink},
		// {title:"和弦", key: 75, action: {type: "chord"}, background: color.pink},
		// {title:"和弦", key: 78, action: {type: "setchord"}, background: color.pink},
		// {title:"三", key: 58, action: {type: "chord", value: 3}, background: color.pink},
		// {title:"七", key: 61, action: {type: "chord", value: 7}, background: color.pink},
		// {title:"九", key: 63, action: {type: "chord", value: 9}, background: color.pink},
		// {title:"十一", key: 66, action: {type: "chord", value: 11}, background: color.pink},
		)
		var question = this.state.questions[this.state.questionIndex]
		if (question && question.choices) {
			var ch=["A","B","C","D"]
			for (var i = 0; i < question.choices.length; i++) {
        		var s = ch[i]
        		var key = 41+i
        		// if (i>1) {
        		// 	key += 1
        		// }
				buttons.push({title:s, key: key, action: {type: "answer", value: i}, background: this.state.answer===i ? color.pink : color.blue})
        	}
        }else if (question && question.answer.notes) {
        	buttons.push({iconfont:iconfont.noteQuarter, key: 41, action: {type: "addNote"}, background: color.blue})
        	if (this.state.addnotes.length>0) {
	        	buttons.push({iconfont:iconfont.arrowUp, key: 42, action: {type: "moveChordLevel", offset: 1}, background: color.blue})
	        	if (this.state.addnotes.length>1) {
		        	buttons.push({iconfont:iconfont.arrowChordSwitch, key: 43, action: {type: "selectChord"}, background: color.blue})
	        	}
	        	buttons.push({iconfont:iconfont.arrowDown, key: 44, action: {type: "moveChordLevel", offset: -1}, background: color.blue})
	        	buttons.push({iconfont:iconfont.accSharp, key: 45, action: {type: "setNoteAlter", offset:1}, background: color.blue})
	        	buttons.push({iconfont:iconfont.accFlat, key: 46, action: {type: "setNoteAlter", offset:-1}, background: color.blue})

	        	buttons.push({iconfont:iconfont.delete, key: 47, action: {type: "delNote"}, background: color.blue})
        	}
		}
        buttons.push({title:"Next", key: 49, action: {type: "checkAnswer"}, background: color.control})
		return <Toolbar items={buttons} action={(action) => this.takeAction(action)}/>
	}
	render() {
		var toolbar = this.scoreToolbar
		var score = this.showQuestion()
		var listview = null
		if (this.state.showListView === ListLevels) {
			var types = ["1级", "2级", "3级", "4级", "5级"]
			listview = <ListView list={types} bottom={lineh*2} action={(index, name) => this.takeAction({type: "selectLevel", value: name})}/>
		}
		if (this.state.questionTypes) {
			var types = " - " + this.state.questionTypes.join(",")
		}
		return (
		<div>
			<div style={{position: "absolute", left: 10, top: 30, width: 365}}>
				<h3>Find音乐学院基本乐科 - {this.state.level}级{types}</h3>
				{score}
				{listview}
			</div>
			{toolbar}
		</div>
		)
	}
}
export default MusicExam;



