import React, { Component } from 'react';
import { Switch, Route, Link } from 'react-router-dom'
import '../App.css';

class Home extends Component {
	constructor(props) {
		super(props)
		this.state = {
			level: 1,
		}
	}
	setLevel(i) {
		window.level=i
		this.setState({level: i})
	}
	render() {
		let levels = []
		for (let i=1; i<6; i++) {
			let className = "Home-button"
			if (i===this.state.level) {
				className += " Home-button-hi"
			}
			levels.push(
				<div key={i} className={className} onClick={(e) => this.setLevel(i) }>
					<p>{i}级</p>
				</div>
				)
		}
		return (<div style={{padding: 10}}>
			<h2>Find音乐学院基本乐科 - {this.state.level}级</h2>
			<div style={{display: 'table'}}>
				{levels}
			</div>
			<div style={{display: 'table'}}>
				<h3>单项测试</h3>
				<Link to='/exam/interval'>
					<div className='Home-icon'>
						<p>音程练习</p>
					</div>
				</Link>
				<Link to='/exam/chord'>
					<div className='Home-icon'>
						<p>和弦练习</p>
					</div>
				</Link>
				<Link to='/exam/scale'>
					<div className='Home-icon'>
						<p>调式音阶练习</p>
					</div>
				</Link>
			</div>
			<div style={{display: 'table'}}>
				<h3>综合测试</h3>
				<Link to='/exam'>
					<div className='Home-icon'>
						<p>综合测试音程、和弦、调式等</p>
					</div>
				</Link>
			</div>
			<div>
				<h3>工具</h3>
				<Link to='/editor'>
					<div className='Home-icon'>
						<p>自由打谱</p>
					</div>
				</Link>
			</div>
		</div>)
	
	}
}

export default Home;



